import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import PrintIcon from "@mui/icons-material/Print";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Grid,
  Step,
  StepLabel,
  Stepper,
  Container,
  Typography,
  useMediaQuery,
  Button,
  Alert,
  IconButton,
} from "@mui/material";
import { makeStyles, useTheme } from "@mui/styles";
import { useDispatch, useSelector } from "react-redux";

import { Box } from "@mui/system";
import { useParams } from "react-router";
import { getStatusesData } from "../../../redux/reducers/ordering";
import { getOrderInfoData } from "../../../redux/reducers/ordering";

const OrderInfo = () => {
  const dispatch = useDispatch();
  const { statuses } = useSelector(({ ordering }) => ordering);
  const { orderInfo } = useSelector(({ ordering }) => ordering);
  const { id } = useParams();
  const classes = useStyles();
  const theme = useTheme();
  const lg = useMediaQuery(theme.breakpoints.down("lg"));
  const [copied, setCopied] = useState(false);
  const [alert, setAlert] = useState(false);
  useEffect(() => {
    dispatch(getOrderInfoData(id));
    dispatch(getStatusesData());
  }, [id]);

  if (!orderInfo) return "Loading...";
  return (
    <>
      <Typography
        variant="h1"
        sx={{
          fontSize: { sm: "30px", xs: "22px" },
          lineHeight: "60px",
          fontWeight: "600",
          mb: { md: "30px", xs: "39px" },
        }}
      >
        Заказ номер {orderInfo.code}
      </Typography>
      <Button
        onClick={() => window.print()}
        variant="contained"
        startIcon={<PrintIcon />}
        sx={{
          "@media print": { display: "none", pageBreakInside: "avoid" },
        }}
      >
        Распечатать
      </Button>
      <section className={classes.section}>
        <Container maxWidth="xl">
          <Box className={classes.paper} mt={8}>
            <Grid container sx={{ alignItems: "center" }}>
              {alert && (
                <Alert
                  severity="success"
                  variant="filled"
                  sx={{
                    position: "fixed",
                    top: "50px",
                    left: 0,
                    right: 0,
                    m: "auto",
                    width: "200px",
                  }}
                >
                  Код скопирован!
                </Alert>
              )}
              <Typography variant="body2">{orderInfo.code}</Typography>
              <IconButton
                onClick={() => {
                  navigator.clipboard.writeText(orderInfo.code);
                  setCopied(true);
                  setAlert(true);
                  setTimeout(() => {
                    setAlert(false);
                  }, 1000);
                }}
              >
                {copied ? (
                  <ContentCopyIcon sx={{ color: "green" }} />
                ) : (
                  <ContentCopyIcon />
                )}
              </IconButton>
            </Grid>

            <Box display="flex" justifyContent="space-between" mt={2} mb={3}>
              <div>
                <Typography
                  variant="subtitle1"
                  sx={{ fontWeight: "500" }}
                  mb={1}
                >
                  {new Date(orderInfo.sending_date).toLocaleDateString()}
                </Typography>
                <Typography variant="body2">
                  {orderInfo.direction[0]?.district.region},{" "}
                  {orderInfo.direction[0]?.district.name}
                </Typography>
              </div>
              <div>
                <Typography
                  variant="subtitle1"
                  sx={{ fontWeight: "500", textAlign: "end" }}
                  mb={1}
                >
                  {/* {orderInfo?.status?.id === 5 &&
                    new Date(orderInfo?.sending_date).toLocaleDateString()} */}
                  {orderInfo?.status?.id !== 5 && <>-</>}
                </Typography>
                <Typography variant="body2">
                  {orderInfo.direction[1]?.district.region},{" "}
                  {orderInfo.direction[1]?.district.name}
                </Typography>
              </div>
            </Box>
            {orderInfo && (
              <Stepper
                activeStep={
                  orderInfo?.status == 6
                    ? 1
                    : orderInfo?.status == 7
                    ? 2
                    : orderInfo?.status == 8
                    ? 3
                    : orderInfo?.status == 9
                    ? 3
                    : orderInfo?.status == 10
                    ? 4
                    : orderInfo?.status == 11
                    ? 6
                    : 7
                }
                alternativeLabel
              >
                {Array.isArray(statuses) &&
                  statuses?.reverse()?.map((status) => (
                    <Step key={status.id}>
                      <StepLabel>
                        <Typography variant="subtitle1">
                          {status.title}
                        </Typography>
                      </StepLabel>
                    </Step>
                  ))}
              </Stepper>
            )}
            {orderInfo && (
              <Accordion className={classes.accordion}>
                <AccordionSummary style={{ width: "100" }}>
                  <Typography
                    variant="body2"
                    sx={{
                      textAlign: "center",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      width: "100%",
                    }}
                  >
                    <span style={{ textDecoration: "underline" }}>
                      Просмотреть описание груза
                    </span>
                    <i
                      className="ri-arrow-down-s-line"
                      style={{
                        marginLeft: 24,
                        fontSize: "24px",
                        display: "inline-block",
                      }}
                    />
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Grid
                    container
                    spacing={3}
                    sx={{
                      "@media print": {
                        flexWrap: "nowrap",
                      },
                    }}
                  >
                    <Grid item xs={12} md={6} lg={4} className={classes.grid}>
                      <Typography
                        variant="body1"
                        sx={{ fontWeight: "500" }}
                        mb={1}
                      >
                        Параметры груза
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                        {` ${orderInfo?.dimension?.height}`}х
                        {orderInfo?.dimension?.width}х
                        {orderInfo?.dimension?.length}, до{" "}
                        {orderInfo?.dimension?.weight} кг
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={6} lg={4} className={classes.grid}>
                      <Typography
                        variant="body1"
                        sx={{ fontWeight: "500" }}
                        mb={1}
                      >
                        Тип доставки
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                        {orderInfo?.payment?.delivery_type?.title} доставка
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={6} lg={4} className={classes.grid}>
                      <Typography
                        variant="body1"
                        sx={{ fontWeight: "500" }}
                        mb={1}
                      >
                        Упаковка
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                        {orderInfo?.payment?.packaging[0]?.title}
                      </Typography>
                    </Grid>
                  </Grid>
                  {!lg && (
                    <Box
                      sx={{
                        "@media print": {
                          display: "none",
                          background: "unset",
                        },
                      }}
                      className={classes.line}
                    />
                  )}
                  <Grid
                    container
                    spacing={3}
                    sx={{
                      "@media print": {
                        flexWrap: "nowrap",
                      },
                    }}
                  >
                    <Grid item xs={12} md={6} lg={4} className={classes.grid}>
                      <Typography
                        variant="body1"
                        sx={{ fontWeight: "500" }}
                        mb={1}
                      >
                        Пункт отправки
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                        {orderInfo.direction[0]?.district.region},{" "}
                        {orderInfo.direction[0]?.district.name}
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={6} lg={4} className={classes.grid}>
                      <Typography
                        variant="body1"
                        sx={{ fontWeight: "500" }}
                        mb={1}
                      >
                        Дата сдачи груза
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                        {new Date(orderInfo.sending_date).toLocaleDateString()},
                        {new Date(orderInfo.sending_date).toLocaleTimeString(
                          [],
                          {
                            timeStyle: "short",
                          }
                        )}
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={6} lg={4} className={classes.grid}>
                      <Typography
                        variant="body1"
                        sx={{ fontWeight: "500" }}
                        mb={1}
                      >
                        Пункт получения
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: "normal" }}>
                        {orderInfo.direction[1]?.district.region},{" "}
                        {orderInfo.direction[1]?.district.name}
                      </Typography>
                    </Grid>
                  </Grid>
                </AccordionDetails>
              </Accordion>
            )}
          </Box>
        </Container>
      </section>
    </>
  );
};

export default OrderInfo;

const useStyles = makeStyles((theme) => ({
  section: {
    padding: { xxl: "80px 0 120px", xs: "0" },
    background: "#F6F7F9",
  },

  paper: {
    background: "#FFFFFF",
    border: "1px solid #C8C8C8",
    boxSizing: "border-box",
    borderRadius: "20px",
    padding: "40px  70px",
    [theme.breakpoints.down("md")]: {
      padding: 30,
    },

    "& .MuiStepConnector": {
      minWidth: 39,
    },

    "& .MuiStep-root": {
      padding: "0 30px",
    },

    "& .MuiStepper-root": {
      overflowX: "scroll",
      overflowY: "hidden",
      "@media print": {
        overflow: "hidden",
      },
    },
  },

  accordion: {
    marginTop: 40,
    boxShadow: "none!important",

    "&::before": {
      display: "none",
    },
  },

  grid: {
    [theme.breakpoints.down("lg")]: {
      borderBottom: "1px solid #EBEBEB",
      paddingBottom: 20,
      "@media print": {
        border: "none",
      },

      "&:last-child, &:nth-child(5)": { border: "none" },
    },

    [theme.breakpoints.down("md")]: {
      "&:nth-child(5)": { borderBottom: "1px solid #EBEBEB" },
      "@media print": {
        border: "none",
      },
    },
  },

  line: {
    height: 1,
    width: "100%",
    background: "#EBEBEB",
    margin: "24px 0",
  },
}));
